<template>
  <div class="header">
    <div class="posi">
      <div class="waiting" v-if="!city">
        <mt-spinner type="fading-circle" color="#fff"></mt-spinner>
      </div>
      <div v-else>
        <div class="position">
          {{ city }}&nbsp;<span class="font ixia"></span>
        </div>
        <p class="temp">{{ wea }}&nbsp;{{ tem }}℃</p>
      </div>
    </div>
    <div class="search">
      <div class="searchbox">
        <div class="searchicon">
          <span class="font isousuo"></span>
        </div>
        <div class="searchinp">
          <input type="text" class="inp" placeholder="聊天" @input="inputchange"/>
        </div>
        <div class="searchbtn" @click="search">搜索</div>
      </div>
    </div>
    <div class="add">
      <span class="font itianjia"></span>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      city: "",
      wea: "",
      tem: "",
      searchData: "",
    };
  },
  mounted() {
    this.get(
      "https://v0.yiketianqi.com/api?version=v61&appid=52152633&appsecret=a1G14628"
    ).then((data) => {
      console.log(data);
      this.city =
        data.city.length > 2 ? data.city.substring(0, 2) + "..." : data.city;
      this.wea = data.wea;
      this.tem = data.tem;
    });
  },
  methods:{
      inputchange(e){
          this.searchData=e.target.value
          console.log(this.searchData)
      },
      search(){
          console.log(this.searchData)
      }
  }
};
</script>
<style lang="less" scoped>
.header {
  height: 50px;
  background: #26a2ff;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  color: #fff;
  font-size: 14px;
  z-index: 1000;
  .search {
    flex: 12;
    padding: 9px 3px;
    .searchbox {
      width: 100%;
      height: 32px;
      display: flex;
      background: #fff;
      border-radius: 5px;
      .searchicon {
        text-align: center;
        line-height: 32px;
        flex: 1;
        color: #333;
      }
      .searchinp {
        flex: 6;
        .inp {
          height: 100%;
          border: none;
          outline: none;
          color: #333;
        }
      }
      .searchbtn {
        height: 26px;
        text-align: center;
        line-height: 26px;
        margin: 3px;
        border-radius: 3px;
        background: rgba(38, 62, 255, 0.1);
        flex: 2;
        color: #26a2ff;
      }
    }
  }
  .add {
    height: 100%;
    flex: 3;
    line-height: 50px;
    text-align: center;
    transform: scale(1.5);
  }
}
.posi {
  height: 100%;
  flex: 4;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.waiting {
  width: 28px;
  height: 28px;
  margin: 0 auto;
}
.position {
  height: 25px;
  line-height: 25px;
  text-align: center;
}
.temp {
  height: 15px;
  line-height: 15px;
  text-align: center;
  font-size: 12px;
}
</style>
